.contact {
  padding: 150px 0;
  background-image: url("../img/contact.jpg");
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-attachment: fixed;

  &__wrapper {
    border-radius: 4px;
    margin: 0 auto;
    padding: 50px 15px;
    max-width: 1000px;
    background-color: #bcbcbca2;
  }

  &__title {
    padding-left: 0;
    text-align: center;
    &::before {
      display: none;
    }
    span {
      padding: 3px 5px;
      font-size: 50px;
      color: var(--light-color);
      background-color: var(--primary-color);
    }
  }
  &__text {
    text-align: center;
  }

  &__btn {
    display: block;
    margin:30px auto 0;
  }
}

.countdown {
  padding: 30px 15px;
  margin: 0 auto 30px;
  font-weight: 600;
  color: var(--light-color);
  text-align: center;
  background-color: var(--primary-red);
  span {
    font-size: 24px;
    font-weight: 700;
  }
}

.form-container label {
  display: block;
  font-weight: bold;
  margin-bottom: 10px;
  color: var(--black-color);
}
.form-container input {
  width: 100%;
  padding: 15px;
  border-radius: 4px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}
.form-container input:focus {
  border-color: #007BFF;
  outline: none;
}

@media screen and (max-width:1024px) {
  .contact {
    &__title {
      span {
        font-size: clamp(1.5rem, 1.396rem + 0.52vw, 1.875rem);
      }
    }
  }
}

@media screen and (max-width:500px) {
.contact {
  padding: 50px 0;
}
}
