html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
p,
ul,
ol {
  margin: 0;
  padding: 0;
}

.page {
  overflow-x: hidden;
  height: 100%;
}

.page__body {
  position: relative;
  overflow-x: hidden;
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-family: var(--font-family);
  font-weight: var(--regular-font);
  font-size: 16px;
}

img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 100%;
  min-height: 100vh;
}

.main {
  position: relative;
  flex: 1 1 auto;
}

.is-hidden {
  display: none !important;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.flex {
  display: flex;
}

.grid {
  display: grid;
}

.centered {
  text-align: center;
}

.page--ios .dis-scroll {
  position: relative;
}

.primary__btn {
  position: relative;
  overflow: hidden;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  width: 320px;
  padding: 20px 10px;
  background-color: var(--primary-color);
  transition: .3s ease-in;

  span {
    position: relative;
    z-index: 2;
    color: var(--light-color);
    line-height: 140%;
    transition: .3s ease-in;
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    border-radius: 4px;
    width: 100%;
    height: 100%;
    background-color: var(--light-color);
    transform: translateX(-100%);
    transition: .3s ease-in;
  }

  &:focus {
    outline: none;
  }

  @include hover {
    span {
      color: var(--primary-color);
    }

    &::before {
      transform: translateX(0);
    }
  }

  &:active {
    &::before {
      background-color: var(--grey-color);
      transform: translateX(0);
    }
  }
}


.section {
  padding: 80px 0;

  &__title {
    position: relative;
    margin-bottom: 30px;
    padding-left: 30px;
    font-size: clamp(1.5rem, 1.396rem + 0.52vw, 1.875rem);
    line-height: 140%;
    text-wrap: balance;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 8px;
      height: 100%;
      background-color: var(--primary-color);
    }
  }

  &__subtitle {
    margin-bottom: 30px;
    font-size: clamp(1.1rem, 1.396rem + 0.52vw, 1.3rem);
    font-weight: 500;
    line-height: 140%;
    text-wrap: balance;
  }

  &__text {
    font-weight: 300;
    line-height: 140%;
    text-wrap: balance;
    &:not(:last-child) {
      margin-bottom: 15px;
    }

    span {
      padding: 3px 5px;
      color: var(--light-color);
      background-color: var(--primary-color);
    }
  }

  &__list {
    margin-bottom: 30px;
    padding-left: 40px;

    &-item {
      position: relative;

      &::before {
        content: "";
        position: absolute;
        left: -30px;
        top: 50%;
        width: 10px;
        height: 10px;
        background-color: var(--primary-color);
        transform: rotate(45deg) translateY(-50%);
      }

      &:not(:last-child) {
        margin-bottom: 15px;
      }

      strong {
        display: block;
        margin-bottom: 5px;
      }
    }
  }
}

@media screen and (max-width:1024px) {
  .section {
    padding: 50px 0;
  }
}

@media screen and (max-width:500px) {
  .primary__btn {
    width: 100%;
  }
}
