.header {
    padding: 10px 0;
    width: 100%;
    background-color: var(--light-color);
    transition: .2s ease-in-out;

    &__body {
        align-items: center;
        justify-content: space-between;
        gap: 5px;
    }

    &__list {
        @include flex-v-center;
        gap: 15px;
    }

    &__item {
        &:nth-child(5) {
            .header__link {
                color: var(--bg-color);
            }
        }
    }

    &__link {
        position: relative;
        display: block;
        overflow: hidden;
        line-height: 125%;
        text-transform: capitalize;
        color: var(--black-color);
        transition: color .3s ease-in-out;

        &::before {
            content: "";
            position: absolute;
            left: 0;
            bottom: 0;
            border-radius: 10px;
            width: 80%;
            height: 1px;
            background-color: var(--primary-red);
            transform: translateX(-100%);
            transition: transform .3s ease-in-out;
        }

        &:focus {
            outline: none;
        }

        @include hover {
            color: var(--primary-red);

            &::before {
                transform: translateX(0);
            }
        }

        &:active {
            color: var(--grey-color);

            &::before {
                background-color: var(--grey-color);
                transform: translateX(0);
            }
        }
    }

    &__logo {

        img {
          width: 320px;
            object-fit: contain;
        }

    }
}

.header--privacy {
    .header__item {
        &:nth-child(5) {
            .header__link {
                color: var(--black-color);
            }
        }
    }
}

.header__link.focus-visible,
.header__link--fill.focus-visible {
    outline: 1px solid rgb(246, 3, 3);
    outline-offset: 3px;
}

.burger {
    position: relative;
    z-index: 1000;
    display: none;
    padding: 0;
    width: 32px;
    height: 20px;
    cursor: pointer;

    &__line {
        position: absolute;
        right: 0;
        top: 50%;
        width: 100%;
        height: 2px;
        background-color: var(--black-color);
        transform: translateY(-50%);
        transition: opacity .2s ease-in-out;
    }

    &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 2px;
        background-color: var(--black-color);
        transition: transform .2s ease-in-out, top .2s ease-in-out;
    }

    &::after {
        content: "";
        position: absolute;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 2px;
        background-color: var(--black-color);
        transition: transform .2s ease-in-out, bottom .2s ease-in-out;
    }

    &--active {
        .burger__line {
            opacity: 0;
        }

        &::before {
            top: 50%;
            background-color: var(--black-color);
            transform: rotate(45deg);
        }

        &::after {
            top: 50%;
            bottom: auto;
            width: 100%;
            background-color: var(--black-color);
            transform: rotate(-45deg);
        }
    }
}

@media screen and (width <=1050px) {
    .header {
        &__body {
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: auto auto;
        }

        &__logo {
            align-items: flex-start;
            grid-row: 1;
            margin-bottom: 32px;
        }

        &__btn {
            grid-row: 1;
            margin-bottom: 32px;
        }

        &__menu {
            grid-column: 1 / span 3;
            width: 100%;
        }

        &__list {
            justify-content: space-between;
            width: 100%;
            gap: 30px;
        }
    }
}

@media screen and (width <=767px) {
    .nav {
        position: fixed;
        right: 0%;
        top: 0;
        z-index: 100;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        overflow: auto;
        padding: 100px 30px;
        width: 100vw;
        height: 100vh;
        background-color: var(--light-color);
        visibility: hidden;
        transform: translateX(100%);
        transition: transform .2s ease-in-out, visibility .2s ease-in-out;

        &--visible {
            visibility: visible;
            transform: translateX(0);
        }
    }

    .header {
        &__body {
            display: flex;
            justify-content: space-between;
        }

        &__btn {
            display: none;
        }

        &__list {
            flex-direction: column;
            align-items: center;
            justify-content: center;
            height: 100%;
            gap: 40px;
        }

        &__link {
            font-weight: var(--bold-font);
            font-size: 22px;
            line-height: 145%;
            text-transform: uppercase;
        }
    }

    .burger {
        display: block;
    }
}

@media screen and (width <=400px) {
  .header {
    &__body {
      flex-direction: column;
    }
  }
}
