.hero {
  position: relative;
  min-height: 800px;
  background-image: url("../img/hero.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-attachment: fixed;
  &__info {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 700px;
    padding: 50px 15px;
  }
  &__title {
    margin-bottom: 50px;
    font-size: clamp(1.75rem, 1.541rem + 1.04vw, 2.5rem);
    line-height: 140%;
    color: var(--light-color);
    text-wrap: balance;
  }

  &__text {
    margin-bottom: 50px;
    max-width: 60%;
    font-size: clamp(1rem, 0.965rem + 0.17vw, 1.125rem);
    line-height: 140%;
    color: var(--light-color);
    text-wrap: balance;
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-color: #00000046;
  }
}


@media screen and (max-width:1000px) {
  .hero {
    &__text {
      max-width: 80%;
    }
  }
}


@media screen and (max-width:500px) {
  .hero {
    min-height: 85vh;
    &__info {
      min-height: 85vh;
    }
    &__title {
      text-align: center;
    }
    &__text {
      max-width: 100%;
      text-align: center;
    }
  }
}
