:root {
  // base
  --font-family: "Montserrat", sans-serif;
  --content-width: 1470px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --bg-color: #bcbcbc;
  --black-color: #000;
  --primary-red: rgb(188, 0, 0);
  --primary-color: #008236;
  --grey-color: #666;
}
