.about {
  &__row {
    padding-left: max(15px, calc((100% - 1470px) / 2));
  }

  &__info {
    flex-shrink: 0;
    width: 600px;
    margin-right: 30px;
  }

  &__image {
    width: calc(100% - 600px - 30px);
    img {
      border-radius: 4px;
    }
  }
}

@media screen and (max-width:1024px) {
  .about {
    &__info {
      width: 450px;
    }

    &__image {
      width: calc(100% - 450px - 30px);
    }
  }
}

@media screen and (max-width:900px) {
  .about {
    &__row {
      flex-direction: column;
      padding: 0 15px;
    }
    &__info {
      width: 100%;
    }

    &__image {
      width: 100%;
    }
  }
}
