.methods {
  &__title {
    padding-left: 0;
    text-align: center;
    &::before {
      display: none;
    }
  }

  &__text {
    margin: 0 auto;
    width: 60%;
    max-width: 100%;
    text-align: center;
  }

  &__row {
    margin-top: 40px;
    gap: 30px;
  }
  &__col {
    flex: 1 1 48%;
  }
}

@media screen and (max-width:1024px) {
  .methods {
    &__text {
      width: 100%;
    }
  }
}

@media screen and (max-width:768px) {
  .methods {
    &__row {
      flex-direction: column;
    }
  }
}
