.symptoms {
  &__row {
    padding-right: max(15px, calc((100% - 1470px) / 2));
  }

  &__info {
    flex-shrink: 0;
    width: 600px;
  }

  &__image {
    margin-right: 30px;
    width: calc(100% - 600px - 30px);
    height: 750px;
    img {
      border-radius: 4px;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

@media screen and (max-width:1024px) {
  .symptoms {
    &__info {
      width: 450px;
    }

    &__image {
      width: calc(100% - 450px - 30px);
    }
  }
}

@media screen and (max-width:900px) {
  .symptoms {
    &__row {
      flex-direction: column-reverse;
      padding: 0 15px;
    }
    &__info {
      margin-bottom: 30px;
      width: 100%;
    }

    &__image {
      height: 500px;
      width: 100%;
    }
  }
}
